import jsonRpc from 'kurento-jsonrpc'
import {
   jsonRpcUrl
} from "@/config/jsonRpc/jsonRpcEnv";

export default {
    DEBUG:false,
    jsonRpcUrl: jsonRpcUrl,
    connected: false,
    jsonRpcClientWs: null,
    sessionId: null,
    roomId: '',
    initMethod: '',
    userId: null,
    vue: null,
    accessType:'no',
    userName:'',
    userEmail:'',
    init(params, vueObj,debug) {
        this.DEBUG = debug;
        if (!params.uid) return;
        if (!params.roomId) return;
        if (this.connected) return;
        if (params.roomHost == 'yes') {
            this.initMethod = 'makeLiveRoom'
        } else {
            this.initMethod = 'joinRoom'
            if(params.accessType)this.accessType = params.accessType;
            if(params.name)this.userName = params.name;
            if(params.email)this.userEmail= params.email;

        }
        this.roomId = params.roomId;
        this.vue = vueObj;
        this.userId = params.uid;


        //setTimeout(()=>{
            this.setConnect();
        //},500)

    },
    setConnect() {
        if(process.env.NODE_ENV =='development') {
            return;
        }

        let protocol = 'wss';
        if (this.DEBUG) {
            protocol = 'ws';
        }
        const ws_uri = protocol+'://' + this.jsonRpcUrl + '/jsonRpc';
        const configuration = {
            hearbeat: 5000,
            sendCloseMessage: false,
            ws: {
                uri: ws_uri,
                useSockJS: false,
                onconnected: () => {
                    this.connected = true;
                    this.socketInit();
                },
                ondisconnect: this.disconnectCallback(this),
                onreconnected: this.connectReCallback(this),
            },
            rpc: {
                requestTimeout: 5000,
                sendResponse: (params) => {
                    this.callMessage(params)
                },
            }
        };
        this.jsonRpcClientWs = new jsonRpc.clients.JsonRpcClient(configuration);

    },

    connectReCallback(obj) {
        obj.connected = true;
    },
    disconnectCallback(obj) {
        obj.connected = false;
    },
    close(type) {

        let params = {};
        params.responseMethod = "liveClose";
        if(process.env.NODE_ENV =='development') {
            params.sendMethod = "liveClose";
            params.userType = type;
            console.log(params,'-------- vue iframe send -----------------')
            const frame = document.getElementById("testIframe").contentWindow;
            frame.postMessage(params, "http://localhost");
            return;
        }
        if (!this.connected) return;
        if (!this.userId) return;
        if (!this.roomId) return;
        params.uid = this.userId;
        params.sessionId = this.sessionId;
        params.userType = type;
        params.roomId = this.roomId;

        this.jsonRpcClientWs.send("liveClose", params, function (error, response) {
            if (error) {
                console.log(error);
            } else {
                console.log("--succ--");
                console.log(response);
            }
        });
    },
    callMessage(params) {

        switch (params.responseMethod) {
            case 'forceHostOut':
                this.vue.$eventBus.$emit('modalOpen', {
                    message: '다른곳에서 강제 방송정지 시켰습니다',
                    doAction: () => {
                        window.close();
                    }

                });
                break;
            default:
                this.vue.$eventBus.$emit('rpcJsonTransfer',params);
                break;

        }

    },
    socketInit() {
        let params = {
            interval: 5000,
            operation: "connect",
            uid: this.userId,
            accessType:this.accessType,
            roomId: this.roomId,
            name:this.userName,
            email:this.userEmail,
            responseMethod:this.initMethod
        };

        this.jsonRpcClientWs.send(this.initMethod, params, (error, response) => {
            if (error) {
                console.log(error);
            } else {
                console.log(response, 'succ');
                this.sessionId = response.sessionId;
                switch (response.map.result) {
                    case 'double':
                        if (this.initMethod == 'makeLiveRoom') {
                            this.vue.$eventBus.$emit('modalOpen', {
                                message: '다른곳에서 이미 방송중입니다',
                                doAction: () => {
                                    window.close();
                                },
                            });

                        } else {
                            this.vue.$eventBus.$emit('modalOpen', {
                                message: '동일 컴퓨터에서 이미 동일한 방에 접근 중입니다',
                                doAction: () => {
                                    window.close();
                                }

                            });

                        }

                        break;
                    case 'fail':
                        this.vue.$eventBus.$emit('modalOpen', {
                            message: '잘못된 접근입니다',
                            doAction: () => {
                                window.close();
                            }

                        });
                        break;
                    case 'success':
                        if (this.initMethod == 'makeLiveRoom' && response.map.waitUsers) {
                            const sendParams = {
                                responseMethod:'makeLiveRoomWaitUsers',
                                waitUsers:response.map.waitUsers,
                            }
                            this.vue.$eventBus.$emit('rpcJsonTransfer',sendParams);

                        }
                        break;
                }

            }

        });

    },
    send(params, func) {

        if(process.env.NODE_ENV =='development') {
            const frame = document.getElementById("testIframe").contentWindow;
            frame.postMessage(params, "http://localhost");
            return;
        }
        if (!this.sessionId) return;
        params.uid = this.userId;
        params.sessionId = this.sessionId;
        params.roomId = this.roomId;
        console.log(params,'--------iframe send -----------------')

        this.jsonRpcClientWs.send(params.sendMethod, params, function (error, response) {
            if (error) {
                console.log(error);
            } else {
                console.log(response);
                func(response);
            }
        });
    },
    forceHostOut() {
        if(process.env.NODE_ENV =='development') {
            const frame = document.getElementById("testIframe").contentWindow;
            frame.postMessage(params, "http://localhost");
            return;
        }
        if (!this.sessionId) return;
        let params = {
            uid: this.userId,
            roomId:this.roomId,
            sessionId: this.sessionId,
            responseMethod: 'forceHostOut'
        };
        this.jsonRpcClientWs.send("forceHostOut", params, function (error, response) {
            if (error) {
                console.log(error);
            } else {
                console.log(response);
            }
        });
    },
    post(frm, afterFunc) {

    }


}
